import Vue from 'vue'
import Router from 'vue-router'
import qindex from '@/pages/q-index/qindex'
import qpic from '@/pages/q-index/qpic'
import qPid from '@/pages/q-index/qPid'
import qPif from '@/pages/q-index/qPif'

Vue.use(Router)

export default new Router({
  mode:"history",
  routes: [
    {
      path: '/',
      name: '',
      component: qindex,
      children:[
        {
          path:"",
          name:"qpic",
          component:qpic
        },
        {
          path:"qPid",
          name:"qPid",
          component:qPid
        },
        {
          path:"qPif",
          name:"qPif",
          component:qPif
        }
      ]
    }
  ]
})



