    <div class="col-md-8">
        <div class="blog-post">
            <h2>红狮MRO.</h2>
            <p><strong>项目描述：</strong>红狮MRO是销售工业配件的线上平台。商城以线上订单+交易的O2O模式。</p>
            <p><strong>项目职责：</strong>根据设计师出对设计稿及产品出的需求文档，分析PC端的页面编码，负责商品详情页布局，JQuery特效添加，以及浏览器兼容性调试；</p>
            <p><strong>项目技术：</strong><br />
                1、Jquery插件实现特效；<br />
                2、根据各个浏览器不同做出兼容性；<br />
                3、熟练使用AJAX进行数据交互，协助后端开发工程师完成项目；<br />
