        <div class="blog-post">
            <h2>红狮水泥商城.</h2>
            <p><strong>项目描述：</strong>红狮水泥商城是销售水泥的线上平台。商城以线上订单+交易的O2O模式。主要模块有列表管理，订单管理，支付管理；本次支付直接对接支付宝；</p>
            <p><strong>项目职责：</strong>本人负责列表页面布局，以及数据的拉去；实现页面的动态刷新；</p>
            <p><strong>项目技术：</strong><br />
                1、HTML+CSS布局;<br />
                2、使用JQuery库及jquery-lazyload等实现特效；<br />
